body {
  margin: 0;
  padding: 0;
  font-family: "Arial", "Helvetica Neue", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", 
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --off-white: #fafafa;
}

.app {
  border: 2px solid gold;
}
.nav {
  background-color: #fafafa;
  background-color: var(--off-white);
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  width: 100%;
  height: 80px;
  z-index: 1000;

}

h3.nav {
  cursor:  pointer;
}

.nav-logo {
  background: linear-gradient(to bottom, 
                                rgba(220, 220, 220, 0) 0%,
                                rgba(220, 220, 220, 0) 62%, 
                                gold 62%, 
                                gold 100%); 
}

.nav-logo:hover {
  cursor: pointer;
}

.nav .nav-content {
  padding: 0rem 3rem;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
}

.nav-item {
  display: inline;
  margin-left: 2rem;
  font-family: 'Arial', 'Helvetica Neue';
  color: #666;
}

.nav-item:hover {
  color: black;
  cursor: pointer;
}

.nav-item > .active {
  border-bottom: 2px solid black;

}

h1 {
  font-family: 'Helvetica Neue', 'Arial';
}
p {
  line-height: 1.5em;
}

h1 > .section {
  margin: 20;
  padding: 20;
}





